
@charset "UTF-8";
.tabs {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  background: #fff;
  -ms-flex-line-pack: center;
      align-content: center;
}
.tabs li {
    -webkit-box-flex: 1;
        -ms-flex: 1;
            flex: 1;
    text-align: center;
    position: relative;
}
.tabs li > div {
      border-right: 1px solid #ddd;
      margin: 10px 0;
}
.tabs li span {
      height: 30px;
      line-height: 30px;
      display: block;
      font-size: 16px;
      color: #1F2D3D;
}
.tabs li.active span:after {
      position: absolute;
      content: 'ã€€';
      width: 80%;
      left: 10%;
      bottom: 0;
      border-bottom: 2px solid #74D5CC;
}
.tabs li:last-of-type > div {
      border-right: 0;
}
.coupon-list {
  margin: 20px;
}
.coupon-list .coupon {
    border: 1px solid #D3DCE6;
    border-radius: 4px;
    position: relative;
    margin-bottom: 15px;
}
.coupon-list .coupon .circle-solid {
      position: absolute;
      width: 5px;
      height: 10px;
      background: #F5F5F5;
}
.coupon-list .coupon .circle-solid.left {
        left: -1px;
        top: 35px;
        border: 1px solid #D3DCE6;
        border-left: 0;
        border-radius: 0 100% 100% 0;
}
.coupon-list .coupon .circle-solid.right {
        right: -1px;
        top: 35px;
        border: 1px solid #D3DCE6;
        border-right: 0;
        border-radius: 100% 0  0 100%;
}
.coupon-list .coupon .circle-solid.left1 {
        left: -1px;
        bottom: 45px;
        border: 1px solid #D3DCE6;
        border-left: 0;
        border-radius: 0 100% 100% 0;
}
.coupon-list .coupon .circle-solid.right1 {
        right: -1px;
        bottom: 45px;
        border: 1px solid #D3DCE6;
        border-right: 0;
        border-radius: 100% 0  0 100%;
}
.coupon-list .coupon .title {
      text-align: center;
      padding-top: 25px;
      font-size: 20px;
      margin-bottom: 25px;
}
.coupon-list .coupon-detail {
      background: #fff;
      border-radius: 0 0  4px 4px;
}
.coupon-list .coupon-detail li {
        word-break: break-all;
        overflow: hidden;
}
.coupon-list .coupon-detail li label {
          float: left;
          width: 80px;
          text-align: left;
          padding-left: 20px;
          color: #1F2D3D;
          font-size: 13px;
}
.coupon-list .coupon-detail li div {
          margin-left: 100px;
          overflow: hidden;
          color: #8492A6;
          padding-right: 20px;
}
.coupon-list .coupon-detail li:last-of-type {
          padding-bottom: 20px;
}
.coupon-list .coupon-detail .opt-group {
        padding: 0 20px;
        font-size: 13px;
        color: #6BC0B8;
        height: 50px;
        line-height: 50px;
        border-top: 1px dashed #D3DCE6;
}
.coupon-list .coupon-detail .btn-share {
        background: #6BC0B8;
        border-radius: 4px;
        margin-top: 10px;
        font-size: 13px;
        color: #FFFFFF;
        width: 64px;
        height: 30px;
        text-align: center;
        display: inline-block;
        line-height: 30px;
        float: right;
}
.coupon-list .coupon .hospotial {
      font-weight: normal;
      line-height: 40px;
      font-size: 14px;
      background: #F9FAFC;
      color: #99A9BF;
      padding: 0 15px;
      border-radius: 4px 4px 0 0;
      border-bottom: 1px dashed #D3DCE6;
}



